import { Component, OnInit } from '@angular/core';
import {SubjectapiService} from '../subjectapi.service';
import {CourseapiService} from '../courseapi.service';
import {CoursesComponent} from '../courses/courses.component';

@Component({
  selector: 'app-subjects',
  templateUrl: './subjects.component.html',
  styleUrls: ['./subjects.component.css']
})
export class SubjectsComponent implements OnInit {

  allSubjects: object[];
  allCourses: object[];

  displayAllSubjectsBool: boolean;
  addSubjectBool: boolean;

  constructor(private subjectApi: SubjectapiService, private courseapi: CourseapiService) {
    this.displayAllSubjectsBool = false;
    this.addSubjectBool = false;
   }

  ngOnInit() {
    this.subjectApi.getAllSubjects().subscribe(
      (data: any) => {
        this.allSubjects = data.json();
      },
      (error: Error) => {
        console.log(error.message);
      }
    );

    this.courseapi.getAllCourses()
    .then((res) => {
      res.subscribe((data) => {
        this.allCourses = data.json();
      });
    });
  }

  displayAllSubjects() {
    this.displayAllSubjectsBool = true;
    this.addSubjectBool = false;
    this.ngOnInit();
  }

  addSubject() {
    this.addSubjectBool = true;
    this.displayAllSubjectsBool = false;
  }

  postNewSubject(data) {

    console.log('here');

    this.subjectApi.addNewSubject(data.subjectName, data.courseId);

  }

}



