import { Component, OnInit } from '@angular/core';
import {StudentapiService} from '../studentapi.service';

@Component({
  selector: 'app-students',
  templateUrl: './students.component.html',
  styleUrls: ['./students.component.css']
})
export class StudentsComponent implements OnInit {

  displayAllStudents: boolean;
  formToAddNewStudent: boolean;

  allStudents: object[];

  constructor(private studentApi: StudentapiService) {
    this.displayAllStudents = false;
    this.formToAddNewStudent = false;
  }

  ngOnInit() {
  }

  async getStudents() {
    this.displayAllStudents = true;
    this.formToAddNewStudent = false;
    await this.studentApi.getAllStudents()
    .then((res) => {
      res.subscribe((data) => {
        this.allStudents = data.json();
      });
    });



  }

  addNewStudent() {
    this.displayAllStudents = false;
    this.formToAddNewStudent = true;
  }

  postNewStudent(data) {
    console.log(this.studentApi.addNewStudent(data.studentName));
  }

}



