import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

@Injectable()
export class BatchapiService {

  BASE_URL = 'http://localhost:3000/courses';

  constructor(private http: Http) { }

  getBatchesByCourseId(courseId: number) {
    return this.http.get(`http://localhost:3000/courses/${courseId}/batches`);
  }

  addNewBatch(batchName: string, courseId: number) {
    return this.http.post(`http://localhost:3000/courses/${courseId}/batches`, {
      name: batchName
    });
  }

}



