import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {HttpModule} from '@angular/http';
import {Routes, RouterModule} from '@angular/router';
import { AppComponent } from './app.component';
import {FormsModule} from '@angular/forms';
import { CoursesComponent } from './courses/courses.component';
import { StudentsComponent } from './students/students.component';
import { BatchesComponent } from './batches/batches.component';
import { LecturesComponent } from './lectures/lectures.component';
import { SubjectsComponent } from './subjects/subjects.component';
import { TeachersComponent } from './teachers/teachers.component';
import {ApiService} from './api.service';
import {CourseapiService} from './courseapi.service';
import {StudentapiService} from './studentapi.service';
import {SubjectapiService} from './subjectapi.service';
import { TeacherapiService } from './teacherapi.service';
import { BatchapiService } from './batchapi.service';

const ALLROUTES = [
  {path: 'courses', component: CoursesComponent},
  // {path: 'courses/:id/batches', component: BatchesComponent},
  {path: 'students', component: StudentsComponent},
  {path: 'lectures', component: LecturesComponent},
  {path: 'subjects', component: SubjectsComponent},
  {path: 'teachers', component: TeachersComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    CoursesComponent,
    StudentsComponent,
    BatchesComponent,
    LecturesComponent,
    SubjectsComponent,
    TeachersComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(ALLROUTES),
    HttpModule,
    FormsModule
  ],
  providers: [
    ApiService,
    CourseapiService,
    StudentapiService,
    SubjectapiService,
    TeacherapiService,
    BatchapiService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



