import { Component, OnInit } from '@angular/core';
import { SubjectapiService } from '../subjectapi.service';
import { TeacherapiService } from '../teacherapi.service';

@Component({
  selector: 'app-teachers',
  templateUrl: './teachers.component.html',
  styleUrls: ['./teachers.component.css']
})
export class TeachersComponent implements OnInit {

  allSubjects: object[];
  allTeachers: object[];

  displayAllTeacherssBool: boolean;
  addTeacherBool: boolean;

  constructor(private teacherApi: TeacherapiService, private subjectApi: SubjectapiService) {
    this.displayAllTeacherssBool = false;
    this.addTeacherBool = false;
   }

  ngOnInit() {
    this.subjectApi.getAllSubjects().subscribe(
      (data: any) => {
        this.allSubjects = data.json();
      },
      (error: Error) => {
        console.log(error.message);
      }
    );

    this.teacherApi.getAllTeachers()
    .then((res) => {
      res.subscribe((data) => {
        this.allTeachers = data.json();
      });
    });

  }

  displayAllTeachers() {
    this.displayAllTeacherssBool = true;
    this.addTeacherBool = false;
    this.ngOnInit();
  }

  addTeacher() {
    this.addTeacherBool = true;
    this.displayAllTeacherssBool = false;
  }

  postNewTeacher(data) {

    this.teacherApi.addNewTeacher(data.teacherName, data.subjectId)
    .subscribe((res) => {
      console.log(res);
    });

  }

}



