import { Component, OnInit } from '@angular/core';
import {CourseapiService} from '../courseapi.service';
import { ActivatedRoute } from '@angular/router';
import { BatchapiService } from '../batchapi.service';

@Component({
  selector: 'app-courses',
  templateUrl: './courses.component.html',
  styleUrls: ['./courses.component.css']
})
export class CoursesComponent implements OnInit {

  allCourses: object[];

  displayBatchesForId: number;

  allBatches: object[];

  displayAllCourses: boolean;

  formToAddNewCourse: boolean;

  displayBatches: boolean;

  constructor(private courseApi: CourseapiService, private route: ActivatedRoute, private batchApi: BatchapiService) {
    this.displayAllCourses = false;
    this.formToAddNewCourse = false;
    this.displayBatches = false;
    this.allBatches = [];
    this.displayBatchesForId = -1;
   }

  ngOnInit() {
  }

  async getCourses() {
    this.displayAllCourses = true;
    this.formToAddNewCourse = false;
    await this.courseApi.getAllCourses()
    .then((res) => {
      res.subscribe((data) => {
        this.allCourses = data.json();
      });
    });



  }

  addNewCourse() {
    this.displayAllCourses = false;
    this.formToAddNewCourse = true;
  }

  postNewCourse(data) {
    console.log(this.courseApi.addNewCourse(data.courseName));
  }

  getBatches(data: number) {

    this.allBatches = [];

    this.batchApi.getBatchesByCourseId(data)
    .subscribe((res) => {
      this.displayBatches = true;
      for (const v of res.json()) {
        for (const batch of v.batches) {
          this.allBatches.push(batch);
        }
      }
      this.displayBatchesForId = data;
    },
    (err) => {
      console.log(err.message);
    }
  );

  }

}



